.generate-box {
  padding: 42px 64px;
  border: 1px solid #e5e7eb;
}
.generate-box-content {
  width: 800px;
  margin-right: 110px;
}
.drag-content .ant-table-tbody tr {
  display: flex;
  width: 480px;
  margin-bottom: 16px;
  border: 1px solid #e5e7eb;
  border-radius: 4px;
}
.drag-content .ant-table-tbody tr td {
  border: none;
}
.drag-content .ant-table-tbody tr td:nth-child(2) {
  width: calc(100% - 46px);
}
.drag-content .ant-table-tbody .drag-details {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.title-with-border {
  margin-top: 50px;
  margin-bottom: 16px;
  padding-bottom: 12px;
  padding-left: 52px;
  box-shadow: inset 0px -2px 0px #e5e7eb;
}
.page-title-with-content h4 {
  font-weight: 600;
  font-size: 16px;
}
.no-data {
  padding-top: 96px;
  text-align: center;
}
.no-data h5 {
  margin: 0 0 8px 0;
  font-weight: 500;
  font-size: 16px;
}
.no-data span {
  color: #6b7280;
  font-size: 12px;
  line-height: 14px;
}
